.top-btn-position {
    position: fixed;
    bottom: 15px;
    right: 20px;
  }
  
  .top-btn-style {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background-color: #fff;
    color: #430711;
    cursor: pointer;
  }
  
  .top-btn-style:hover {
    animation: none;
    background-color: #FFAB00;
    color: #fff;
    border: 2px solid #fff;
  }
  